import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const [isDisplayProgress, setIsDisplayProgress] = React.useState(false);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    { dataField: "areaName", text: "Area", type: "string", sort: true },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    {
      dataField: "ripenessLevel",
      text: "Ripeness Level",
      type: "string",
      sort: true,
    },
    { dataField: "qty", text: "QTY", type: "number", sort: true },
    { dataField: "newPack", text: "Pack", type: "string", sort: true },
    {
      dataField: "outboundType",
      text: "Outbound Type",
      type: "string",
      sort: true,
    },
    {
      dataField: "outboundReason",
      text: "Outbound Reason",
      type: "string",
      sort: true,
    },
  ];

  const dataSet1 = [
    {
      areaName: "",
      po: "",
      skuNumber: "",
      ripenessLevel: "",
      qty: "",
      outboundType: "",
      outboundReason: "",
    },
  ];

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="AdditionalOutbound_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Area" value="area" />
          <ExcelColumn label="PO SKU Code" value="po" />
          <ExcelColumn label="SKU Number" value="skuNumber" />
          <ExcelColumn label="Expiry Date" value="expirydate" />
          <ExcelColumn label="Ripeness Level" value="ripenessLevel" />
          <ExcelColumn label="QTY" value="qty" />
          <ExcelColumn label="Pack" value="pack" />
          <ExcelColumn label="Outbound Type" value="grade" />
          <ExcelColumn label="Outbound Reason" value="ripeness" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push(
            "/inventory-entry/additional-outbound/CSV-upload/review"
          )
        }
        transaction={"additiona_outbound"}
        field={Field}
        type="additionalOutbound"
      />
    </div>
  );
};

export default withRouter(Upload);
